:root {
    background-color: rgb(0, 0, 0, 1);
    max-width: 1280px;
    min-width: 320px;
    min-height: 100vh;
    margin: 0 auto;
    font-family: 'OpenSans', 'Buyan';
    color: rgba(242, 244, 250);
    text-rendering: optimizeLegibility;
    scroll-behavior: smooth;
    -moz-osx-font-smoothing: grayscale;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    letter-spacing: .015em;
    padding: 20px;
    --back-mage: url(../../images/Others/back.svg);
    --font-one-color: rgba(221, 29, 29, 1);
    --font-two-color: rgba(77, 189, 252, 1);

    /* Новогоднее оформление сайта */
    background-image:
        url("data:image/png;base64,"),
        url("data:image/png;base64,"),
        url("data:image/png;base64,");
    -webkit-animation:
        snowy 9s linear infinite;
    -moz-animation:
        snowy 9s linear infinite;
    -o-animation:
        snowy 9s linear infinite;
    -ms-animation:
        snowy 9s linear infinite;
    animation:
        snowy 9s linear infinite;
}

@-moz-keyframes snowy {
    0% {
        background-position: 0px 0px, 0px 0px, 0px 0px;
    }

    75% {
        background-position: 325px 750px, 275px 600px, -5px 150px;
    }

    100% {
        background-position: 500px 1000px, 400px 800px, 0px 300px;
    }
}

@-webkit-keyframes snowy {
    0% {
        background-position: 0px 0px, 0px 0px, 0px 0px;
    }

    75% {
        background-position: 325px 750px, 275px 600px, -5px 150px;
    }

    100% {
        background-position: 500px 1000px, 400px 800px, 0px 300px;
    }
}

@-o-keyframes snowy {
    0% {
        background-position: 0px 0px, 0px 0px, 0px 0px;
    }

    75% {
        background-position: 325px 750px, 275px 600px, -5px 150px;
    }

    100% {
        background-position: 500px 1000px, 400px 800px, 0px 300px;
    }
}

@keyframes snowy {
    0% {
        background-position: 0px 0px, 0px 0px, 0px 0px;
    }

    75% {
        background-position: 325px 750px, 275px 600px, -5px 150px;
    }

    100% {
        background-position: 500px 1000px, 400px 800px, 0px 300px;
    }
}