/* Slider */
.slick-slider
{
    margin: 0 0 40px;
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
    width: 60%;
    max-height: 500px;
    object-fit: contain;
    border-radius: 20px;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}
@media screen and (max-width: 1368px) {
    .slick-slider {
        margin: 0 0 35px;
        max-height: 100%;
    }
}
@media screen and (max-width: 1180px) {
    .slick-slider {
        margin: 0 0 35px;
        max-height: 100%;
    }
}
@media screen and (max-width: 1024px) {
    .slick-slider {
        max-height: 100%;
        margin: -1px auto 20px;
    }
}
@media screen and (max-width: 768px) {
    .slick-slider {
        margin: 0 0 -3.5px;
    }
}
@media screen and (max-width: 320px) {
    .slick-slider {
        margin: 15px 0;
        max-height: 111.5px;
    }
    .slick-slide img {
        width: 70%;
    }
}
@media screen and (max-width: 280px) {
    .slick-slider {
        margin: 0;
        max-height: 111.5px;
    }
    .slick-slide img {
        width: 70%;
    }
}